import React, { useEffect, useState } from 'react';
import GitHubLogo from './assets/github-mark-white.svg';
import LinkedInLogo from './assets/linkedin-white.png';

export default function Bio() {
  const [hoverGitHub, setHoverGitHub] = useState(false);
  const [hoverLinkedIn, setHoverLinkedIn] = useState(false);
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  const [isMobile, setIsMobile] = useState(false);
  const paraFontSize = isMobile ? '16px' : '24px'; // 24px on desktop, 18px on mobile (767px)
  const linkFontSize = isMobile ? '14px' : '18px'; // 18px on desktop, 14px on mobile (767px)
  
  useEffect(() => {
    const handleResize = () => setWindowWidth(window.innerWidth);
    window.addEventListener('resize', handleResize);
    if (windowWidth < 767) {
      setIsMobile(true);
    }
    else if (windowWidth >= 767) {
        setIsMobile(false);
    }
    return () => window.removeEventListener('resize', handleResize);
  }, [windowWidth]);
  
  return (
    <div
      className="name-container"
      style={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'flex-start',
        justifyContent: 'flex-start',
        paddingLeft: '5vw',
        paddingTop: '2vh',
        marginRight: isMobile ? '30vw' : '0vw', // 5vw on mobile, 0vw on desktop (767px)
      }}
    >
      <h1
        style={{
          color: 'white',
          fontSize: isMobile ? '28px' : '38px', // 48px on desktop, 36px on mobile (767px)
          fontWeight: '400',
        }}
      >
        Brian Matzelle
      </h1>
      <p1
        style={{
          color: 'white',
          fontSize: paraFontSize,
          fontWeight: '350',
        }}
      >
        Welcome, I'm a Computer Science senior at Binghamton University.
      </p1>
      <p2
        style={{
          paddingTop: '2vh',
          color: 'white',
          fontSize: paraFontSize,
          fontWeight: '350',
        }}
      >
        I have experience with full-stack development,
      </p2>
      <p2
        style={{
          color: 'white',
          fontSize: paraFontSize,
          fontWeight: '350',
        }}
      >
        mobile development (iOS and Android),
      </p2>
      <p2
        style={{
          color: 'white',
          fontSize: paraFontSize,
          fontWeight: '350',
        }}
      >
        and artificial intelligence.
      </p2>
      <p3
        style={{
          paddingTop: '2vh',
          color: 'white',
          fontSize: paraFontSize,
        }}
      >
        I'm currently looking for a software engineering internship.
      </p3>
      <a
        href="mailto: brian@matzelle.co"
        style={{
          paddingTop: '2vh',
          color: '#005A43',
          fontSize: linkFontSize,
          fontWeight: '350',
        }}
      >
        brian@matzelle.co
      </a>
      <a
        href="https://github.com/brianmatzelle"
        onMouseEnter={() => setHoverGitHub(true)}
        onMouseLeave={() => setHoverGitHub(false)}
        style={{
          display: 'flex',
          flexDirection: 'row',
          alignItems: 'center',
          justifyContent: 'flex-start',
          paddingTop: '1vh',
          color: '#005A43',
          fontSize: linkFontSize,
          fontWeight: '350',
          textDecoration: hoverGitHub ? 'underline' : 'none',
        }}
      >
        {/* Show GitHub logo svg from ./assets/github-mark-white.svg */}
        <img
          src={GitHubLogo}
          alt="GitHub Logo"
          style={{
            height: '16px',
            marginRight: '7px',
          }}
        />
        GitHub
      </a>
      <a
        href="https://www.linkedin.com/in/brianmatzelle/"
        onMouseEnter={() => setHoverLinkedIn(true)}
        onMouseLeave={() => setHoverLinkedIn(false)}
        style={{
          display: 'flex',
          flexDirection: 'row',
          alignItems: 'center',
          justifyContent: 'flex-start',
          paddingTop: '.5vh',
          color: '#005A43',
          fontSize: linkFontSize,
          fontWeight: '350',
          textDecoration: hoverLinkedIn ? 'underline' : 'none',
        }}
      >
        {/* Show LinkedIn logo svg from ./assets/linkedin-white.png */}
        <img
          src={LinkedInLogo}
          alt="LinkedIn Logo"
          style={{
            height: '16px',
            marginRight: '7px',
            borderRadius: '20%',
          }}
        />
        LinkedIn
      </a>
    </div>
  );
}    
